import React, { Component } from "react";
import "../styles/BuyPage.css";

import MyFrom2 from "../components/MyForm2";
import Carousel from "react-elastic-carousel";

import pinguin from "../images/Products/pluszak.png";
import sheep from "../images/Products/sheep.png";
import krolik from "../images/Products/królik.png";
import rabbit from "../images/Products/rabbit.png";

class BuyPage extends Component {
  state = {
    active: true,
    activePingwin: false,
    activeOwieczka: false,
    activeKrolik: false,
    activeZajac: false,
  };
  render() {
    return (
      <>
        <div className="buyPage">
          <div className="orderBg"> </div>
          <h1 className={this.state.active ? null : "activeH1Buy"}>
            Tu znajdziesz rzeczy już gotowe, <br /> uszyte przeze mnie
            wcześniej, które możesz mieć od razu
          </h1>
          <section className={this.state.active ? "toBuyNow2" : "toBuyNow"}>
            <ul>
              <li
                onClick={() =>
                  this.setState({
                    active: false,
                    activePingwin: true,
                    activeOwieczka: false,
                    activeKrolik: false,
                    activeZajac: false,
                  })
                }
              >
                <Carousel breakPoints={1}>
                  <img src={pinguin} alt="pinguin" />
                  <img src={pinguin} alt="pinguin" />
                </Carousel>
                <p>
                  PINGWINEK <br /> Cena: 80zł
                </p>
              </li>
              <li
                onClick={() =>
                  this.setState({
                    active: false,
                    activePingwin: false,
                    activeOwieczka: true,
                    activeKrolik: false,
                    activeZajac: false,
                  })
                }
              >
                <Carousel breakPoints={1}>
                  <img src={sheep} alt="owieczka" />
                  <img src={sheep} alt="owieczka" />
                </Carousel>
                <p>
                  OWIECZKA <br /> Cena: 100zł
                </p>
              </li>
              <li
                onClick={() =>
                  this.setState({
                    active: false,
                    activePingwin: false,
                    activeOwieczka: false,
                    activeKrolik: true,
                    activeZajac: false,
                  })
                }
              >
                <Carousel breakPoints={1}>
                  <img src={krolik} alt="krolik" />
                  <img src={krolik} alt="krolik" />
                </Carousel>
                <p>
                  KRÓLIK
                  <br /> Cena: 150zł
                </p>
              </li>
              <li
                onClick={() =>
                  this.setState({
                    active: false,
                    activePingwin: false,
                    activeOwieczka: false,
                    activeKrolik: false,
                    activeZajac: true,
                  })
                }
              >
                <Carousel breakPoints={1}>
                  <img src={rabbit} alt="rabbit" />
                  <img src={rabbit} alt="rabbit" />
                </Carousel>
                <p>
                  ZAJĄCZEK
                  <br /> Cena: 80zł
                </p>
              </li>
            </ul>
          </section>
          {this.state.activePingwin ? (
            <div className="aboutAndBuy">
              <section className="aboutAndBuyText">
                <span>
                  <h3>PINGWINEK</h3>
                  <p>
                    CENA: <strong> 80 zł</strong>
                  </p>
                  <p>
                    {" "}
                    ROZMIAR: <strong> ok. 15cm</strong>
                  </p>
                  <p>
                    PIELĘGNACJA: <strong> Pranie - 30°</strong>
                  </p>
                  <p>
                    MATERIAŁ: <strong> Bawełna 55%, Poliester 45%</strong>{" "}
                  </p>
                </span>
              </section>
              <span className="myForm2">
                <h3>Napisz do mnie i zamów</h3>
                <MyFrom2 />
              </span>
            </div>
          ) : null}
          {this.state.activeOwieczka ? (
            <div className="aboutAndBuy">
              <section className="aboutAndBuyText">
                <span>
                  <h3>OWIECZKA</h3>
                  <p>
                    CENA: <strong> 100 zł</strong>
                  </p>
                  <p>
                    {" "}
                    ROZMIAR: <strong> ok. 20cm</strong>
                  </p>
                  <p>
                    PIELĘGNACJA: <strong> Pranie - 30°</strong>
                  </p>
                  <p>
                    MATERIAŁ: <strong> Bawełna 55%, Poliester 45%</strong>{" "}
                  </p>
                </span>
              </section>
              <span className="myForm2">
                <h3>Napisz do mnie i zamów</h3>
                <MyFrom2 />
              </span>
            </div>
          ) : null}
          {this.state.activeKrolik ? (
            <div className="aboutAndBuy">
              <section className="aboutAndBuyText">
                <span>
                  <h3>KRÓLIK</h3>
                  <p>
                    CENA: <strong> 150 zł</strong>
                  </p>
                  <p>
                    {" "}
                    ROZMIAR: <strong> ok. 30cm</strong>
                  </p>
                  <p>
                    PIELĘGNACJA: <strong> Pranie - 30°</strong>
                  </p>
                  <p>
                    MATERIAŁ: <strong> Bawełna 55%, Poliester 45%</strong>{" "}
                  </p>
                </span>
              </section>
              <span className="myForm2">
                <h3>Napisz do mnie i zamów</h3>
                <MyFrom2 />
              </span>
            </div>
          ) : null}
          {this.state.activeZajac ? (
            <div className="aboutAndBuy">
              <section className="aboutAndBuyText">
                <span>
                  <h3>ZAJĄCZEK</h3>
                  <p>
                    CENA: <strong> 80 zł</strong>
                  </p>
                  <p>
                    {" "}
                    ROZMIAR: <strong> ok. 20cm</strong>
                  </p>
                  <p>
                    PIELĘGNACJA: <strong> Pranie - 30°</strong>
                  </p>
                  <p>
                    MATERIAŁ: <strong> Bawełna 55%, Poliester 45%</strong>{" "}
                  </p>
                </span>
              </section>
              <span className="myForm2">
                <h3>Napisz do mnie i zamów</h3>
                <MyFrom2 />
              </span>
            </div>
          ) : null}
        </div>
      </>
    );
  }
}

export default BuyPage;
